.header-photographe__position-items {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    margin-top: 10px;
}

.header-photographe__logo-style {
    width: 260px;
    height: 80px;
    cursor: pointer;
}

@media screen and (max-width: 700px) {
    .header-photographe__logo-style {
        width: 150px;
        height: 50px;
    }
    
}